.site-nav {
    margin: 0;
    padding: 0;
    font-size: 0;

    &__item {
        display: inline-block; }

    &__link {
        position: relative;
        display: inline-block;
        color: inherit;
        padding: 5px 2px;
        text-decoration: none;
        text-transform: uppercase;

        &::after {
            content: "";
            display: none;
            width: 100%;
            position: absolute;
            left: 0;
            background-color: $base-c; }

        &:hover,
        &.active {
            &::after {
                display: block; } } } }

.header__nav {
    .site-nav {
        &__item {
            &:not(:last-child) {
                margin-right: 30px; } }

        &__link {
            font-size: $small-fz;
            font-family: $bold-f;

            &::after {
                height: 3px;
                bottom: -5px; } } } }

.footer__nav {
    .site-nav {
        width: 530px;
        display: flex;
        justify-content: space-between;

        &__link {
            font-size: 12px;

            &::after {
                height: 1px;
                bottom: -5px; } } } }
