.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    user-select: none;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent; }

.slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0; }

.slick-slide:focus,
.slick-list:focus {
    outline: none; }

.slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
    transform: translate3d(0, 0, 0); }

.slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    margin-left: auto;
    margin-right: auto; }

.slick-track:before,
.slick-track:after {
    display: table;
    content: ''; }

.slick-track:after {
    clear: both; }

.slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
    display: none;
    float: left;
    height: 100%;
    min-height: 1px; }

.slick-slide img {
    display: block; }

.slick-slide.slick-loading img {
    display: none; }

.slick-slide.dragging img {
    pointer-events: none; }

.slick-initialized .slick-slide {
    display: block; }

.slick-loading .slick-slide {
    visibility: hidden; }

.slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow {
    position: absolute;
    top: 134px;
    color: #A1A3A3;
    font-size: 0;

    &:hover {
        color: $light-c; }

    &::before {
        font-size: 32px; }

    &.slick-hidden {
        display: none; } }

.slick-prev {
    left: -60px;
    &::before {
        content: "\e900";
        font-family: $icon-f; } }

.slick-next {
    right: -60px;
    &::before {
        content: "\e902";
        font-family: $icon-f; } }
