.container {
    position: relative;
    width: 1300px;
    margin-right: auto;
    margin-left: auto; }

.layout-section {
    margin-top: 40px; }

.base-layout {
    display: flex;
    align-items: flex-start;

    &__main {
        width: 860px;
        flex-shrink: 0;
        margin-right: 20px; }

    &__aside {
        width: 420px;
        padding-left: 40px;
        flex-shrink: 0; } }

.aside-widget {
    &:not(:last-child) {
        margin-bottom: 20px; } }

.light-bg {
    background-color: #F5F6F7;
    padding-bottom: 40px; }

.light-bg + .layout-section,
.light-bg + footer {
    margin-top: 0; }
