.aside-nav-widget {
    min-height: 240px;
    padding: 28px 30px;
    background-color: $light-c;
    border-radius: 4px;
    border: 1px solid #d2d7dd; }

.aside-nav {

    &__list {
        margin: 0;
        padding: 0; }

    &__item {
        &:not(:last-child) {
            margin-bottom: 20px; } }

    &__link {
        display: inline-flex;
        align-items: center;
        padding-right: 20px;
        font-size: $mini-fz;
        font-family: $bold-f;
        line-height: $base-lh;
        letter-spacing: 0.02em;
        color: inherit;
        text-decoration: none;
        border-radius: 18px;

        &:hover,
        &:focus {
            background-color: #eff2f5;
            color: $base-c; }

        &.active {
            background-color: #eff2f5; } }

    &__icon {
        width: 36px;
        height: 36px;
        margin-right: 20px; } }

