.reviews {
    margin-top: 40px;
    margin-bottom: 20px;

    &__title-wrap {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        padding-bottom: 10px;
        border-bottom: 1px solid #7A8BA1; }

    &__title {
        margin-right: 20px;
        font-size: 36px;
        font-family: $bold-f;
        color: $dark-c; }

    &__counter {
        min-width: 48px;
        height: 48px;
        padding-left: 10px;
        padding-right: 10px;
        font-size: 36px;
        line-height: 50px;
        font-family: $bold-f;
        color: $light-c;
        letter-spacing: 0.02em;
        text-align: center;
        background: linear-gradient(#00caff 0%, $base-c 100%);
        border-radius: 24px; }

    &__btn {
        margin-top: 40px; } }

.review {
    &:not(:last-child) {
        margin-bottom: 40px; }

    &__head {
        display: flex;
        align-items: center;
        margin-bottom: 20px; }

    &__date {
        margin-right: 20px;
        font-size: 14px;
        letter-spacing: 0.08em;
        color: $base-gray; }

    &__name {
        font-family: $bold-f;
        font-size: 14px;
        letter-spacing: 0.02em; }

    &__text {
        font-size: 16px;
        line-height: $base-lh; } }
