input {
    margin: 0; }

input[type=password] {
    letter-spacing: .1em; }

input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
    -webkit-text-fill-color: $light-c;
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    transition: background-color 5000s ease-in-out 0s;

    &:hover:not(:focus) {
        -webkit-box-shadow: 0 0 0px 1000px transparent inset; } }

 ::-webkit-input-placeholder {
    color: $base-gray;
    letter-spacing: initial !important; }

 ::-moz-placeholder {
    color: $base-gray;
    letter-spacing: initial !important; }

 ::-ms-input-placeholder {
    color: $base-gray;
    letter-spacing: initial !important; }

 ::placeholder {
    color: $base-gray;
    letter-spacing: initial !important; }

.disabled,
*:disabled {
    @extend %disabled; }

.input-wrap {
    position: relative;

    &_password {
        position: relative; } }

.input-base {
    padding: 11px 15px 10px;
    font-size: 14px;
    line-height: $micro-lh; }

.input-big {
    width: 100%;
    padding: 10px 20px 9px;
    font-size: 24px;
    line-height: $micro-lh;
    border-radius: 4px;
    background-color: $light-c;
    border: 1px solid #d7dbe0; }

.input-line {
    width: 100%;
    padding: 5px 15px 15px;
    font-size: 14px;
    color: $light-c;
    background-color: transparent;
    border-bottom: 1px solid $light-c; }

.textarea-base {
    display: block;
    padding: 8px 16px;
    line-height: $base-lh; }

.input-base,
.textarea-base {
    width: 100%;
    color: $dark-c;
    background-color: $light-c;

    &:focus:not(.has-error) {
        border-color: $base-dark-c; } }

.has-error {
    border-color: $error-c !important; }
